body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #210002;
}
/* PARALLAX */

.body {
  margin: -5px;
}

#name {
  width: 100%;
  position: fixed;
  text-align: center;
  top: 20vh;
}

#name h1{
  font-size: 6vw;
}

.titletext {
  color: #210002;
}

#links {
  color: #210002;
  position: fixed;
  top:40vh;
  width: 100%;
}

.brownbtn {
  color: #210002;
  border-color: #210002;
  display: block;
  margin: auto;
  margin-top: 5vh;
  margin-bottom: 5vh;
}

#links a {
  text-decoration: none;
}
.brownbtn:hover, .brownbtn:active, .brownbtn.active, .open {
  background-color: #210002;
}

