.keyart, .keyart_layer {
  height: 1000px;
}

#parallax {
  display: block;
}

#nonparallax {
  display: none;
}

#nonparallax {
  background-image: url(/NewPersonalWebsiteReact/static/media/keyart-mobile.dc65f346.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
}

.keyart {
  position: relative;
  z-index: -10;
}

.keyart_layer {
  background-position: bottom center;
  background-size: auto 1038px;
  background-repeat: repeat-x;
  width: 100%;
  position: absolute;
}
.keyart_layer.parallax {
  position: fixed;
}
  #keyart-0 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax0.bac00e42.png);
    background-color: #ffaf1b;
  }
  #keyart-1 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax1.de4f2e14.png);
  }
  #keyart-2 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax2.f9e45588.png);
  }
  #keyart-3 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax3.2c1791eb.png);
  }
  #keyart-4 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax4.85af35fc.png);
  }
  #keyart-5 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax5.22944f56.png);
  }
  #keyart-6 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax6.62e88f15.png);
  }
  #keyart-7 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax7.9aadff21.png);
  }
  #keyart-8 {
    background-image: url(/NewPersonalWebsiteReact/static/media/parallax8.a1124ff2.png);
  }
  #keyart-scrim {
    background-color: #ffaf00;
    opacity: 0;
  }

@media screen and (max-width: 600px){ 
  #nonparallax {
    display: block;
  }
  #parallax {
    display: none;
  }
  #name h1{
    font-size: 12vw;
  }
}
#about {
	background-color: #210002;
	width: 100%;
	/*height: 100vh;*/
	display: inline-block;
	vertical-align: middle;
	position: relative;
}

#portrait {
  background-image: url(/NewPersonalWebsiteReact/static/media/portrait.10c46e81.png);
  background-repeat: no-repeat;
  position: absolute;
  background-size: 20vw;
  height: 20vw;
  width: 20vw;
  top: 50%; left: 20%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  display: inline-block;
}

#description {
	/*position: absolute;*/
	width: 50%;
	left: 40%;
	/*display: inline-block;*/
	margin-left: 40%; margin-right: 10%;
	margin-top: 20vh; margin-bottom: 10vh;
}
#description p{
	font-size: 17px;
	color: white;
	word-wrap: break-word;
}

@media screen and (max-width: 600px){ 

#about {
	display: block;
}

#portrait {
	position: absolute;
	background-size: 50vw;
	width: 50vw;
	height: 50vw;
	top: -50vw;
	left: 50%;
	-webkit-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
}

#description {
	width: 80%;
	margin: auto;
	margin-top: 50vw;
}

}
#gallery {
	background-color: #210002;
	position: relative;
}

#tiles {
	display: inline-block;
	background-color: #210002;
	position: relative;
	width: 80%;
	left: 50%;
	-webkit-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
	margin-bottom: 10vh;
}

.tile-wrapper {
	width: 25%;
	float: left;
    overflow: hidden;
}

.tile {
	width: 100%;
	filter: gray;
    -webkit-filter: grayscale(1);
    -webkit-transition: all .3s ease-in-out;
}

.tile:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
	filter: none;
    -webkit-filter: grayscale(0);
}

@media screen and (max-width: 600px){ 
.tile-wrapper {
	width: 50%;
}
}
#footer {
	background-color: #210002;
	text-align: center;
}

#social {
	display: inline-block;
	width: 60%;
	margin: auto;
}

#social img {
	width: 5vw;
}

#social a {
	margin-left: 2vw; margin-right: 2vw;
}

#copyright {
	color: #d3d3d3;
}

#copyright p {
	margin-bottom: 0;
	font-size: 12px;
}
